import { getDistance } from "geolib";
import warehouses from "src/data/warehouses.json";

export const findGeo = (position: GeolocationPosition) => {
  let min = { distance: 100000000000000, index: -1 };

  for (let i = 0; i < warehouses.length; i++) {
    const { latitude, longitude } = position.coords;
    const distance = getDistance(warehouses[i].geo, { latitude, longitude });

    if (distance < min.distance) {
      min = { distance, index: i };
    }
  }
  return warehouses[min.index];
};
