import { FC, useEffect } from "react";
import { Layout } from "src/components/layout/layout";
import { Link, useNavigate } from "react-router-dom";
import { useChangeLanguage } from "src/hooks/use-change-language";
import { useShallow } from "zustand/react/shallow";
import { useStore } from "src/store/store";
import { useAnalitics } from "src/hooks/use-analitics";
import { findGeo } from "src/utils/find-geo";
import warehouses from "src/data/warehouses.json";
import languageBtns from "src/data/languages-btns.json";
import classNames from "classnames";
import style from "./warehouses-page.module.css";

const WarehousesPage: FC = () => {
  const { t, translation, changeLanguage } = useChangeLanguage();

  const navigate = useNavigate();

  const {
    setHouseName,
    setHouseData,
    setGeoPermission,
    setGeoLoading,
    geoLoading,
    geoPermission,
  } = useStore(
    useShallow(state => ({
      setHouseName: state.setHouseName,
      setHouseData: state.setHouseData,
      setGeoPermission: state.setGeoPermission,
      setGeoLoading: state.setGeoLoading,
      geoLoading: state.geoLoading,
      geoPermission: state.geoPermission,
    })),
  );

  const geolocationHandler = () => {
    !geoLoading &&
      navigator.permissions.query({ name: "geolocation" }).then(result => {
        setGeoLoading(true);
        navigator.geolocation.getCurrentPosition(
          position => {
            const house = findGeo(position);
            setGeoPermission(result.state);
            setHouseName(house.name);
            navigate(`/${house.query}`);
          },
          () => setGeoPermission(result.state),
        );
      });
  };

  useEffect(() => {
    setHouseName("/");

    setHouseData(null);

    setGeoLoading(false);
  }, [setHouseName, setHouseData, setGeoLoading]);

  useAnalitics();

  return (
    <Layout>
      <article className={style.warehouses}>
        <h1>{t("main-title")}</h1>
        <section className={style.warehouses__languages}>
          <h2>{t("select-language")}</h2>
          <form>
            {languageBtns.map(btn => (
              <div key={btn.id} className={style.languages__input}>
                <input
                  checked={btn.id === translation}
                  id={btn.id}
                  type="radio"
                  name="language"
                  onChange={changeLanguage}
                />
                <label htmlFor={btn.id}>{btn.text}</label>
              </div>
            ))}
          </form>
        </section>
        <section className={style.warehouses__list}>
          <h2>{t("select-warehouse")}</h2>
          <div>
            {warehouses.map(house => (
              <Link
                key={house.query}
                to={`/${house.query}`}
                onClick={() => setHouseName(house.name)}
              >
                {t(`${house.name}`)}
              </Link>
            ))}
          </div>
        </section>
        <button
          onClick={geolocationHandler}
          className={classNames(style.geobtn, geoLoading && style.loading)}
        >
          {t("select-btn")}
        </button>
        <div
          style={{ display: geoPermission === "denied" ? "block" : "none" }}
          className={style.warehouses__text}
        >
          {t("geolocation-block")}
        </div>
      </article>
    </Layout>
  );
};
export default WarehousesPage;
